import React, { useEffect } from 'react';
import {useDispatch, useSelector} from 'react-redux';
import {
    CONNECT_WEB_SOCKET
} from '../../redux/actionTypes';
import {
    listenUserList,
    listenPublicMessage,
    listenPrivateMessage
} from '../../WebSocketService'
import MessagePanel from './Components/MessagePanel';
import MessageSender from './Components/MessageSender';
import {stateInterface} from '../../Interfaces';

const ChatRoom = () => {  
    const dispatch = useDispatch();
    const {webSocket, isMobile} = useSelector((state: stateInterface) => state.chatRoomReducer);
    
    ///Connect web socket
    useEffect(() => {
        if(webSocket){
            listenPublicMessage(webSocket, dispatch, isMobile);
            listenPrivateMessage(webSocket, dispatch, isMobile);
            listenUserList(webSocket, dispatch);
        }else{
            dispatch({type: CONNECT_WEB_SOCKET});
        }
    }, [webSocket]);

    return (
        <main id='mainGrid'>
            <MessagePanel/>
            <MessageSender/>
        </main>
    );
}

export default ChatRoom;